<template>
  <div class="ucloud-cell">
    <template v-if="renderType === 'index'">{{naturalIndex + 1}}</template>
    <template v-else>
      <span>{{row[column.field]}}</span>
    </template>
  </div>
</template>
<script>
  export default {
    props: {
      renderType: '',
      naturalIndex: Number,
      column: Object,
      row: Object
    }
  }
</script>




