<template>
  <div class="ucloud-table" :style="caculateStyle">
    <div class="ucloud-table-head" ref="header">
      <u-head :columns="columns" :tableWidth="tableWidth"></u-head>
    </div>
    <div class="ucloud-table-body" @scroll="handleBodyScroll" :style="{height: height}">
      <u-body :columns="columns" :tableWidth="tableWidth" :data="data"></u-body>
    </div>
    <div class="ucloud-table-fixed" :style="fixedTableStyle">
      <u-head :columns="columns" :tableWidth="tableWidth"></u-head>
      <div class="ucloud-table-fixed-body" ref="fixedBody" :style="{height: fixBodyHeight}">
        <u-body :columns="columns" :tableWidth="tableWidth" :data="data" :fixed="true" :leftFixedColumns="leftFixedColumns"></u-body>
      </div>

    </div>
  </div>
</template>
<script>
  import './table.less'
  import uBody from './table-body.vue'
  import uHead from './table-head.vue'
  export default {
    name: 'u-table',
    components: {
      uBody,
      uHead
    },
    props: {
      height: {
        type: String,
        default: function () {
          return 'auto'
        }
      },
      columns: {
        type: Array
      },
      data: {
        type: Array
      }
    },
    computed: {
      headHeight () {
        return '20px'
      },
      fixBodyHeight () {
        if (this.height !== 'auto') {
          return this.height.replace('px', '') - this.headHeight.replace('px', '') + 'px'
        } else {
          return 'auto'
        }
      },
      caculateStyle () {
        let style = {
          width: '100%',
          maxWidth: this.tableWidth + 'px'
        }
        return style
      },
      tableWidth () {
        return this.columns.map(cell => {
          return cell.hidden ? 0 : (cell.width || 0)
        }).reduce((a, b) => a + b, 0)
      },
      leftFixedColumns () {
        let left = []
        let other = []
        this.cloneColumns.forEach((col) => {
          if (col.fixed) {
            left.push(col)
          } else {
            other.push(col)
          }
        })
        return left.concat(other)
      },
      fixedTableStyle () {
        let style = {}
        let width = 0
        this.leftFixedColumns.forEach((col) => {
          if (col.fixed && !col.hidden) {
            width += col.width
          }
        })
        style.width = `${width}px`
        return style
      },
      cloneColumns () {
        let clonedCol = []
        this.columns.forEach(item => {
          clonedCol.push(Object.assign({}, item))
        })
        return clonedCol
      }
    },
    methods: {
      handleBodyScroll (event) {
        this.$refs.header.scrollLeft = event.target.scrollLeft
        this.$refs.fixedBody.scrollTop = event.target.scrollTop
      }
    }
  }
</script>



