export default {
  methods: {
    setCellWidth (column, index, top) {
      let width = ''
      if (column.width) {
        width = column.width
      } else {
        width = ''
      }
      if (width === '0') {
        width = ''
      }
      if (width.toString().indexOf('px') === -1 && width.toString().indexOf('%') === -1) {
        width += 'px'
      }
      return width
    }
  }
}



