<template>
  <div class="ucloud-body">
    <table cellspacing="0" cellpadding="0" border="0" :style="{width:tableWidth+'px'}">
      <colgroup>
        <col v-for="(column, index) in effectedCols" v-if="!column.hidden" :width="setCellWidth(column, index)">
      </colgroup>
      <template v-for="(row, index) in data">
        <tr>
          <!--当前table是fixed同事对应列也是fixed-->
          <td v-for="column in effectedCols" v-if="!column.hidden" :class="{fixed:fixed && column.fixed,visibilityhidden:fixed && !column.fixed}">
            <Cell :row="row" :column="column" :index="index" :natural-index="index"></Cell>
          </td>
        </tr>
      </template>
    </table>
  </div>
</template>
<script>
  import Mixin from './mixin'
  import Cell from './cell.vue'
  import _ from 'lodash'
  export default {
    mixins: [Mixin],
    props: {
      columns: Array,
      data: Array,
      leftFixedColumns: Array,
      fixed: {
        type: Boolean,
        default: false
      },
      tableWidth: Number
    },
    components: {
      Cell
    },
    computed: {
      // 过滤掉colspan的列
      effectedCols () {
        return _.filter(this.columns, (item) => {
          return !item.colspan
        })
      }
    }
  }
</script>



