    <p>以下仅为demo具体交互需要高强辅助设计一下</p>
    <draggable v-model="computedCol" :options="{group:'outside',animation:150}" @start="drag=true" @end="drag=false">
      <div v-for="(element, index) in computedCol" class="ctn" :class="{draggable:!element.fixed || !element.colspan}">
        <span class="fa fa-eye" :class="{active:!element.hidden}" @click.stop="setVisible(element, index)"></span>{{element.title}}
        <div class="child-ctn">
          <draggable v-model="element.children" :options="{group:'inside',animation:150}" @start="drag=true" @end="drag=false">
            <div v-for="item in element.children" class="child-item">
              {{item.title}}
            </div>
          </draggable>
        </div>

      </div>
    </draggable>
    <div style="margin-bottom: 20px"></div>
    <div style="    display: none;
    background-color: rgb(65, 184, 131);
    border: 1px solid rgb(238, 238, 238);
    margin: 10px;
    padding: 5px 10px;
    color: #FFF;
    border-radius: 4px;" @click="convert2Datatable">重绘</div>
    <!--<u-table :columns="columns" :data="data" height="200px" style="width: 780px"></u-table>-->
    <u-table :columns="columnsMulti" :data="data" height="200px"></u-table>
    <table border="1" style="display: none">
