            name: 'name1',
            index: 1,
            materialName: '物料名称1',
            taxrate: '17%',
            noTaxAcceptancePrice: 20.00,
            acceptancePrice: 24.00,
            supEnterpriseName: '供应商名称',
            purchaseamount: 6
          }, {
            id: 2,
            name: 'name2',
            index: 1,
            materialName: '物料名称1',
            taxrate: '17%',
            noTaxAcceptancePrice: 20.00,
            acceptancePrice: 24.00,
            supEnterpriseName: '供应商名称',
            purchaseamount: 10
          }
        ],
        cloneColumnsMulti: []
      }
    },
    computed: {
      computedCol: {
        get: function () {
          return this.convertColumns2TreeColumns()
        },
        set: function (newValue) {
          this.cloneColumnsMulti = this.convertTreeColumns2Columns(newValue)
          this.convert2Datatable()
        }
      }
    },
    methods: {
      formatColumn (array) {
        array.forEach(item => {
          item.hidden = item.hidden ? item.hidden : false
        })
        return array
      },
      setVisible (element, index) {
        element.hidden = !element.hidden
        element.children.forEach(item => {
          item.hidden = !element.hidden
        })
      },
      convert2Datatable () {
        this.columnsMulti = this.convertTreeColumns2Columns(this.computedCol)
      },
      convertTreeColumns2Columns (tree) {
        var result = []
        tree.forEach(item => {
          result.push(item)
          if (item.children && item.children.length > 0) {
            result = _.concat(result, item.children)
          }
        })
        return result
      },
      convertColumns2TreeColumns () {
        var _col = this.cloneColumnsMulti.length > 0 ? this.cloneColumnsMulti : this.columnsMulti
        var result = []
        for (var i = 0; i < _col.length; i++) {
          var item = _col[i]
          item.children = []
          // 如果有存在列合并的情况
          if (_col[i].colspan) {
            for (var j = 1; j <= _col[i].colspan; j++) {
              _col[i].children.push(_col[i + j])
            }
            i = i + item.colspan
          }
          if (item) {
            result.push(item)
          }
        }
        console.log(result)
        return result
      }
    }
  }
</script>

