}
  .ctn .child-ctn{
    display: none;
    position: relative;
    background-color: #FFF;
    border: solid 1px #eee;
    border-radius: 2px;
    box-shadow: 2px 0 6px -2px rgba(0, 0, 0, 0.2);
  }
  .ctn .child-ctn .child-item{
