<template>
  <div class="ucloud-head">
    <table cellspacing="0" cellpadding="0" border="0" :style="{width:tableWidth+'px'}">
      <colgroup>
        <col v-for="(column, index) in effectedCols" v-if="!column.hidden" :width="setCellWidth(column, index)">
      </colgroup>
      <thead>
        <tr>
          <th v-for="(column, index) in mainCols" v-if="!column.hidden" :colspan="column.colspan" :rowspan="column.rowspan">
            <div>
              <span v-if="!column.renderHeader" :title="column.title">{{ column.title || '' }}</span>
            </div>
          </th>
        </tr>
        <tr v-if="subCols.length > 0">
          <th v-for="(column, index) in subCols" v-if="!column.hidden" :colspan="column.colspan" :rowspan="column.rowspan">
            <div>
              <span v-if="!column.renderHeader" :title="column.title">{{ column.title || '' }}</span>
            </div>
          </th>
        </tr>
      </thead>
    </table>
  </div>
</template>
<script>
  import Mixin from './mixin'
  import _ from 'lodash'
  export default {
    mixins: [Mixin],
    props: {
      columns: Array,
      tableWidth: Number
    },
    computed: {
      groupedCol () {
        return _.groupBy(this.columns, item => {
          return item.row
        })
      },
      mainCols () {
        return this.groupedCol[undefined]
      },
      subCols () {
        // 2表示第二行 目前只打算支持到两行表头
        return this.groupedCol['2'] || []
      },
      effectedCols () {
        return _.filter(this.columns, (item) => {
          return !item.colspan
        })
      }
    }
  }
</script>



